
























import App from '../../../../../../App.vue'
import Api from '../../../../../../api.js'
import tkiQrcode from '../../../../../components/tki-qrcode/tki-qrcode.vue'
export default {
	data() {
		return {
			scrollTop: 0,
			account_id: '',
			userid: '',
			userinfor: {},
			operation: []
		}
	},
	onLoad() {
		var that = this
		uni.request({
			url: Api.addoperator(),
			header: {
				token: App.getToken()
			},
			data: {
				companyId: App.getcompany().company_id
			},
			success: function(res) {
				console.log(res)
				if (res.data.code == 200) {
					for (let iterm in res.data.value) {
						if (res.data.value[iterm].level == 4) {
							that.userid = res.data.value[iterm].userid
							console.log(that.userid)
							uni.request({
								url: Api.seeotheruser(),
								header: {
									token: App.getToken(),
								},
								data: {
									otherId: that.userid
								},
								success(res) {
									console.log(res)
									that.userinfor = {
											image: res.data.user.author_image,
											operate: res.data.user.author_name,
											userid: res.data.user.author_id
										},
										that.operation.push(that.userinfor)
									console.log(that.operation)
								}
							})
						}
					}
				} else {
					uni.showToast({
						title: '您没有这个权限',
						icon: 'none'
					})
				}
			}
		})
	},
	methods: {
		errorImage: function(index) {
			this.operation[index].image=App.geturlerror(this.operation[index].image)
		},
		deleteOperator: function(index) {
			var that = this
			console.log(that.operation[index].userid)
			uni.request({
				url: Api.addoperator(),
				method: 'DELETE',

				header: {
					"Content-Type": "application/x-www-form-urlencoded",
					token: App.getToken()
				},
				data: {
					companyId: App.getcompany().company_id,
					userid: that.operation[index].userid
				},
				success(res) {
					console.log(res)
					that.operation.splice(index, 1)
				}
			})

		},
		addOperator: function() {
			var that = this
			uni.scanCode({
				success: function(res) {
					that.account_id = res.result;
					console.log(that.account_id)
					uni.request({
						url: Api.addoperator(),
						method: 'POST',
						header: {
							token: App.getToken(),
							"Content-Type": "application/x-www-form-urlencoded"
						},
						data: {
							companyId: App.getcompany().company_id,
							account: that.account_id,
							level: true
						},
						success: function(res) {
							console.log(res)
							console.log(res.data.value.userid)
							that.userid = res.data.value.userid
							console.log(that.userid)
							if (res.data.code == 200) {
								uni.request({
									url: Api.seeUser(),
									header: {
										token: App.getToken(),
									},
									data: {
										otherId: that.userid
									},
									success(res) {

										console.log(res)
										that.userinfor = {
												image: res.data.user.author_image,
												operate: res.data.user.author_name
											},
											that.operation.push(that.userinfor)
										console.log(that.operation)
									}
								})
							} else if (res.data.code == 1006) {
								uni.showToast({
									title: '此员工已经存在',
									icon: 'none'
								})
							} else {
								uni.showToast({
									title: '您没有权限',
									icon: 'none'
								})
							}
						}

					})
				}
			})
		}

	}
}
